.main {
  height: 100vh;
  width: 100%;
  overflow-y: auto;

  .container {
    display: grid;
    max-width: 1120px;
    margin: 0 auto;
    padding: 35px 0 97px 0;

    @media (max-width: 1024px) {
      max-width: 940px;
    }
    @media (max-width: 768px) {
      max-width: 700px;
    }
    @media (max-width: 425px) {
      max-width: 373px;
    }
    @media (max-width: 375px) {
      max-width: 339px;
    }
    @media (max-width: 320px) {
      max-width: 280px;
    }
  }
}
